.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 15px;
    padding: 0;
    box-sizing: border-box;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    float: left;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 3px;
    background: #f8f8f8;
}

.toggle-button__line:nth-child(2) {
    width: 24px;
}

@media screen and (max-width: 1024px) {

    .toggle-button {
        margin-top: 1.1rem;
    }

}

@media screen and (max-width: 768px) {

    .toggle-button {
        margin-top: 1rem;
    }
    
}