@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html {
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

.container {
  width: 80%;
  overflow: hidden;
  margin: 0 auto;
}

.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 15px;
    padding: 0;
    box-sizing: border-box;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    float: left;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 3px;
    background: #f8f8f8;
}

.toggle-button__line:nth-child(2) {
    width: 24px;
}

@media screen and (max-width: 1024px) {

    .toggle-button {
        margin-top: 1.1rem;
    }

}

@media screen and (max-width: 768px) {

    .toggle-button {
        margin-top: 1rem;
    }
    
}
:root {
    --primary-color: #f2695c;
    --secondary-color: #1e1e1e;
    --primary-text-color: #f8f8f8;
}

.aing-top {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #1e1e1e;
    background-color: var(--secondary-color);
    color: #f8f8f8;
    color: var(--primary-text-color);
}

.drawerResponsive {
    display: none;
}

.aing-nav__logo {
    float: left;
    width: 20%;
}

.aing-nav__logo a {
    color: #f8f8f8;
    color: var(--primary-text-color);
    text-decoration: none;
}

.aing-nav__logo h1 {
    margin-top: 7px;
    font-size: 2.2rem;
    font-weight: 700;
    letter-spacing: .15rem;
}

.aing-navigation {
    float: left;
    text-align: center;
    width: 60%;
}

.aing-navigation .aing-navbar__nav {
    list-style: none;
    text-align: center;
}

.aing-navigation .aing-navbar__nav .aing-nav__item {
    display: inline-block;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link {
    position: relative;
    display: block;
    padding: 15px;
    font-size: 1.2rem;
    text-decoration: none;
    text-transform: uppercase;
    color: #f8f8f8;
    color: var(--primary-text-color);
    font-weight: 700;
    margin: 0 10px;
    letter-spacing: .1rem;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link:hover {
    color: #f2695c;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link::before {
    position: absolute;
    bottom: 8px;
    left: 0;
    right: 0;
    margin: auto;
    width: 0%;
    height: 1px;
    content: '';
    background-color: #f2695c;
    background-color: var(--primary-color);
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link:hover::before {
    width: 100%;
}

.aing-socials {
    display: inline-block;
}

.aing-socials a {
    color: #f8f8f8;
    color: var(--primary-text-color);
    font-size: 1.2rem;
    margin-top: 7px;
    display: inline-block;
    padding: 10px 15px;
}

.aing-socials a:hover {
    color: #f2695c;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link,
.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link::before,
.aing-socials a {
    transition: all .5s;
}

@media screen and (max-width: 1024px) {

    .aing-top {
        min-height: 60px;
    }

    .aing-nav__logo h1 {
        display: none;
    }

    .aing-navigation .aing-navbar__nav .aing-nav__item,
    .aing-socials {
        display: none;
    }

}

@media screen and (max-width: 1024px) {

    .drawerResponsive {
        display: block;
    }

}

@media screen and (max-width: 768px) {

    .aing-top {
        min-height: 60px;
    }

    .aing-nav__logo h1 {
        display: none;
    }

    .aing-navigation .aing-navbar__nav .aing-nav__item,
    .aing-socials {
        display: none;
    }
    
}

:root {
    --primary-color: #f2695c;
    --secondary-color: #1e1e1e;
    --primary-text-color: #f8f8f8;
}

.side-drawer {
    height: 100%;
    background-color: #1e1e1e;
    background-color: var(--secondary-color);
    box-shadow: 2px 0 5px rgba(0, 0, 0, .16);
    position: fixed;
    top: 0;
    left: 0;
    width: 80%;
    max-width: 400px;
    z-index: 200;
    transition: all .4s;
    transform: translateX(-100%);
}

.side-drawer.open {
    transform: translateX(0);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: .5rem 5rem;
}

.side-drawer li:nth-child(2) {
    margin-bottom: 20px !important;
}

.side-drawer a {
    position: relative;
    color: #f8f8f8;
    color: var(--primary-text-color);
    font-size: 1.2rem;
    text-decoration: none;
    font-weight: 700;
    text-transform: uppercase;
    transition: all .25s;
}

.side-drawer .socials {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 90%;
    margin: 0 auto;
    padding-top: 20px;
    border-top: 1px solid rgba(255, 255, 255, .3);
}

.side-drawer ul li a::before {
    content: '';
    position: absolute;
    width: 0;
    height: 1px;
    left: 0;
    right: 0;
    bottom: -3px;
    margin: auto;
    background-color: #f2695c;
    background-color: var(--primary-color);
    transition: all .25s;
}

.side-drawer ul li a:hover::before {
    width: 100%;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: #f2695c;
    color: var(--primary-color);
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .3);
    z-index: 100;
}
