:root {
    --primary-color: #f2695c;
    --secondary-color: #1e1e1e;
    --primary-text-color: #f8f8f8;
}

.side-drawer {
    height: 100%;
    background-color: var(--secondary-color);
    box-shadow: 2px 0 5px rgba(0, 0, 0, .16);
    position: fixed;
    top: 0;
    left: 0;
    width: 80%;
    max-width: 400px;
    z-index: 200;
    transition: all .4s;
    transform: translateX(-100%);
}

.side-drawer.open {
    transform: translateX(0);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: .5rem 5rem;
}

.side-drawer li:nth-child(2) {
    margin-bottom: 20px !important;
}

.side-drawer a {
    position: relative;
    color: var(--primary-text-color);
    font-size: 1.2rem;
    text-decoration: none;
    font-weight: 700;
    text-transform: uppercase;
    transition: all .25s;
}

.side-drawer .socials {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 90%;
    margin: 0 auto;
    padding-top: 20px;
    border-top: 1px solid rgba(255, 255, 255, .3);
}

.side-drawer ul li a::before {
    content: '';
    position: absolute;
    width: 0;
    height: 1px;
    left: 0;
    right: 0;
    bottom: -3px;
    margin: auto;
    background-color: var(--primary-color);
    transition: all .25s;
}

.side-drawer ul li a:hover::before {
    width: 100%;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: var(--primary-color);
}