:root {
    --primary-color: #f2695c;
    --secondary-color: #1e1e1e;
    --primary-text-color: #f8f8f8;
}

.aing-top {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background-color: var(--secondary-color);
    color: var(--primary-text-color);
}

.drawerResponsive {
    display: none;
}

.aing-nav__logo {
    float: left;
    width: 20%;
}

.aing-nav__logo a {
    color: var(--primary-text-color);
    text-decoration: none;
}

.aing-nav__logo h1 {
    margin-top: 7px;
    font-size: 2.2rem;
    font-weight: 700;
    letter-spacing: .15rem;
}

.aing-navigation {
    float: left;
    text-align: center;
    width: 60%;
}

.aing-navigation .aing-navbar__nav {
    list-style: none;
    text-align: center;
}

.aing-navigation .aing-navbar__nav .aing-nav__item {
    display: inline-block;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link {
    position: relative;
    display: block;
    padding: 15px;
    font-size: 1.2rem;
    text-decoration: none;
    text-transform: uppercase;
    color: var(--primary-text-color);
    font-weight: 700;
    margin: 0 10px;
    letter-spacing: .1rem;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link:hover {
    color: #f2695c;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link::before {
    position: absolute;
    bottom: 8px;
    left: 0;
    right: 0;
    margin: auto;
    width: 0%;
    height: 1px;
    content: '';
    background-color: var(--primary-color);
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link:hover::before {
    width: 100%;
}

.aing-socials {
    display: inline-block;
}

.aing-socials a {
    color: var(--primary-text-color);
    font-size: 1.2rem;
    margin-top: 7px;
    display: inline-block;
    padding: 10px 15px;
}

.aing-socials a:hover {
    color: #f2695c;
}

.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link,
.aing-navigation .aing-navbar__nav .aing-nav__item .aing-nav__link::before,
.aing-socials a {
    transition: all .5s;
}

@media screen and (max-width: 1024px) {

    .aing-top {
        min-height: 60px;
    }

    .aing-nav__logo h1 {
        display: none;
    }

    .aing-navigation .aing-navbar__nav .aing-nav__item,
    .aing-socials {
        display: none;
    }

}

@media screen and (max-width: 1024px) {

    .drawerResponsive {
        display: block;
    }

}

@media screen and (max-width: 768px) {

    .aing-top {
        min-height: 60px;
    }

    .aing-nav__logo h1 {
        display: none;
    }

    .aing-navigation .aing-navbar__nav .aing-nav__item,
    .aing-socials {
        display: none;
    }
    
}
